<template>
  <form @submit.prevent="addAlarmItem" class="alarmPanelContainer card p10">
    <div class="imgBox">
      <img src="/img/alarm.png" alt="alarm" style="width: 100%">
    </div>
    <toggle-button
      :labels="{checked: 'Sell', unchecked: 'Buy'}"
      :color="{checked: 'red', unchecked: 'limegreen'}"
      @change="toggleDirection"
      style="width:50px"
      :value="radioDirection"
    />
    <input type="text" class="coinBox" name="coin" placeholder="coin" v-model='formState.coin' required>
    <input  type="text" class="amBox" name="price" placeholder="amount" v-model.number='formState.amount' required>
    <input  type="text" class="prBox" name="price" placeholder="price" v-model.number='formState.desiredPrice' required>
    <input type="text" class="commentBox" name="comment" placeholder="comment" v-model='formState.comment'>
    <button type="submit" class="hoverNegative">add</button>
  </form>
</template>

<script>
  import generateRandomId from '../../helpers/generateRandomId';
  import Popup from '../Msg';

  export default {
    components: { Popup },
    data() {
      return {
        radioDirection: false,
        formState: {
          coin: '',
          desiredPrice: '',
          comment: '',
          amount: '',
        },
      };
    },
    computed: {
      direction() {
        if (this.radioDirection) {
          return -1
        } else {
          return 1
        }
      },
    },
    methods: {
      addAlarmItem() {
        const item = {
          id: generateRandomId(),
          coin: this.formState.coin.toLowerCase(),
          date: new Date().toLocaleString('en-US', {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit'
          }),
          desiredPrice: this.formState.desiredPrice,
          comment: this.formState.comment,
          amount: this.direction * this.formState.amount,
        }

        if(this.$store.state.isAK){
          this.$store.commit('addAlarm',{item,info:'infoAK',listName:'alarmAltsList'})
        }else{
          console.log('add to me')
          this.$store.commit('addAlarm',{item,info:'infoMain',listName:'alarmAltsList'})
        }
        this.resetForm();
      },
      resetForm(){
        this.formState.coin = '';
        this.formState.desiredPrice = '';
        this.formState.comment = '';
        this.formState.amount = '';
      },
      toggleDirection({ value }) {
        this.radioDirection = value;
      },
    }
  }

</script>

<style scoped>
  .alarmPanelContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  .alarmPanelContainer>*{
    display: inline-block;
    margin-right: 20px;
  }
  .imgBox{
    width:40px;
  }
  .toggleBox{
    width: 100px;
  }
  @media only screen and (max-width: 768px) {
    .alarmPanelContainer{
      flex-flow: column;
    }
    .alarmPanelContainer>*{
      flex-flow: column;
      margin-bottom: 10px;
    }
  }

</style>
